function getChoices(rowIndex, colIndex){
  let choices = [];
  
  choices.push(syllablesTable[rowIndex][colIndex]);

  for(var i=9;i >0 &&  choices.length < 4;i-- ){
    const j = Math.floor(Math.random() * (10));
    // console.log('j',j,'syllablesTable[rowIndex]',syllablesTable[rowIndex],'am', syllablesTable[rowIndex][j]);

    if (!choices.find((e) => e === syllablesTable[rowIndex][j])){
      choices.push(syllablesTable[rowIndex][j]);
    }
  }

  choices = shuffleArray( choices);
  
  return choices;
}

// Hàm chuyển âm sang tiếng Việt không dấu
export function getExercises () {
  var exercises = [];
  syllablesTable.map((row, rowIndex) => (
      row.map((syllable, colIndex) => {
        exercises.push({ id: rowIndex*100+colIndex, 
          mp3: `/static/phatam/${rowIndex > 8 ? rowIndex+1: '0' + (rowIndex+1)}${colIndex<9?colIndex+1:'a'}${removeVietnameseTones(syllable)}.mp3`, 
          choices: getChoices(rowIndex, colIndex), 
          correctAnswer: syllable });
        
      }) 
  ));

  return exercises;
};

// Hàm chuyển âm sang tiếng Việt không dấu
export function removeVietnameseTones (str) {
  return str
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, '')
    .replace(/đ/g, 'd')
    .replace(/Đ/g, 'D');
};

